
.container {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
}


.main {
    @extend .container;
    z-index: 1050;
    box-shadow: 0 8px 14px 0 rgba(0,0,0,0.30);
    background:$color-white;
    font-family: $sans;
    font-size: 28px;
    color:$color-grey;
    letter-spacing: 0;
    line-height: 40px;
    padding:0 60px 0  60px;
    margin-bottom: 120px;
    
}


.footer {
    background:$color-white;
    width: 100%;
    height: 75px;
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 0;
    z-index: 10;
    padding-top:10px;
    
}
.footer a{
    padding-top:10px;
    color:$color-grey !important; 
}

.header {

    &--nav {
        background:$color-white;
        width: 100%;
        height: 75px;
        display: flex;
        justify-content: center;
        padding-top:20px;
        }
    
    &--title {
        font-weight: $font-weight-bold;
        font-size: 50px;
        color: $color-grey;
        font-family: $sans;
        margin: 60px 0;
        display:flex;
        justify-content: center;
    }


}

.box {
    @extend .container;
    
    box-shadow: 0 8px 14px 0 rgba(0,0,0,0.30);
    background:$color-white;
    font-family: $sans;
    font-size: 28px;
    color:$color-grey;
    letter-spacing: 0;
    line-height: 18px;
   
}


.since {
    font-family: $sans;
    font-size: 18px;
    font-weight: 800;
    color: $color-grey;
    letter-spacing: 0;
    line-height: 18px;
    display: block;
    line-height: 25px;
  }


.pink {
    color: $color-pink;
  }
